body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.wrap-logo{
  padding: 1em 9% 0;
}
.logo {
  width: 50px;
  height: 50px;
}
.line {
  height: .7px;
  background: grey;
  margin-top: 1em;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  -webkit-animation: grow-left cubic-bezier(0.785, 0.135, 0.15, 0.86) 1s forwards;
          animation: grow-left cubic-bezier(0.785, 0.135, 0.15, 0.86) 1s forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}
@-webkit-keyframes grow-left {
  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}
@keyframes grow-left {
  from {
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  to {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }
}
.wrap-hero {
  position: relative;
}
.hero {
  display: flex;
  height: 100vh;
}
.left, .right {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.the-text {
  margin-top: 10em;
  width: 65%;
  z-index: 1;
}
.title, .subtitle {
  font-size: 4.2em;
  font-family: Montserrat;
  font-weight: 900;
}
.subtitle {
  font-weight: 300;
  font-size: 1.8em;
}
.subtitle2 {
  font-size: 1.4em;
  font-family: Poiret One;
  font-style: italic;
  padding-top: 1em;
}
.arrow {
  margin-top: 7em;
  width: 60px;
  height: 60px;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}
.arrow:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.right {
  flex: 1 1;
  background: #dca829;
  position: relative;
}
.heroPicture {
  position: absolute;
  left: -10%;
  width: 100%;
  margin: auto;
  display: block;
}

@media (max-width: 600px) {
  .hero {
    display: block;
  }
  .left {
    width: 100%;
    margin: auto;
  }
  .right {
    width: 100%;
  }
  .the-text {
    width: 80%;
    margin-bottom: 3em;
  }
  .arrow  {
    display: none;
  }
  .heroPicture {
    position: relative;
    width: 100%;
    left: 0;
    margin-top: 4em;
  }
}
.wrap-description {
  width: 60%;
  margin: 10em 10%;
}
.title-description {
  font-size: 2em;
  font-weight: 900;
  font-family: Montserrat;
}
.text-description {
  margin-top: 3em;
}
@media (max-width: 600px) {
  .wrap-description {
    width: 80%;
    margin: 4em auto;
  }
}
.button {
  margin: auto;
  display: block;
  padding: 0.5em 2em;
  font-size: 1.5em;
  font-family: Montserrat;
  background: none;
  border-radius: 30px;
  outline: none;
  cursor: pointer;
}
a {
  text-decoration: none;
}

* {
  margin: 0;
  padding: 0;
}
